.App {
  text-align: center;
  color: white;
  min-height: 100vh;
  background: #2980b9;
  background: -webkit-linear-gradient(to top, #2c3e50, #2980b9);
  background: -webkit-gradient(linear, left bottom, left top, from(#2c3e50), to(#2980b9));
  background: -webkit-linear-gradient(bottom, #2c3e50, #2980b9);
  background: -o-linear-gradient(bottom, #2c3e50, #2980b9);
  background: linear-gradient(to top, #2c3e50, #2980b9);
}

.header {
  height: 100px;
  padding: 20px 0px;
  color: white;
}

.title {
  font-size: 2.5em;
  text-transform: uppercase;
  text-shadow: 2px 3px 0px #898999;
  letter-spacing: 0.08em;
}

.button {
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  padding: 12px 18px;
  letter-spacing: 0.095em;
  border-radius: 20px;
  background-color: #2980b9;
  border: none;
  color: white;
  -webkit-appearance: none;
}

.button:hover {
  background-color: #2473a6;
}

.button__reset {
  height: 35px;
  width: 80px;
  -webkit-box-shadow: 5px 5px 14px 1px rgba(0,0,0,0.1);
          box-shadow: 5px 5px 14px 1px rgba(0,0,0,0.1);
}

.button:focus {
  outline: none;
}

.button__modal {
  width: 110px;
  background-color: rgba(41, 128, 185, 0.9);
}

.modal__overlay {
  z-index: 98;
  position: fixed;
  top: 0%;
  bottom: 0%;
  left: 0%;
  right: 0%;
  background: rgba(0, 0, 0, .3);
}

.modal__content {
  z-index: 99;
  position: absolute;
  background-color: rgba(32, 102, 148, 0.8);
  border: 1px solid rgba(32, 102, 148, 0.9);
  font-size: 22px;
  letter-spacing: 0.1em;
  color: white;
  width: 250px;
  height: 150px;
  top: 50%;
  left: 50%;
  text-align: center;
  text-transform: uppercase;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  -webkit-box-shadow: 5px 5px 14px 1px rgba(0,0,0,0.1);
          box-shadow: 5px 5px 14px 1px rgba(0,0,0,0.1);
}

.modal_text {
  margin-top: 35px;
  opacity: 0.85;
}

.options {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: end;
      align-items: flex-end;
  margin: 50px 0px;
  padding-left: 90px;
}

.label {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-right: 15px;
  font-size: 0.9em;
  font-weight: 300;
  text-transform: uppercase;
}

.input {
  border: none;
  border-bottom: 1px solid white;
  border-radius: 0px;
  height: 28px;
  width: 56px;
  padding-left: 10px;
  text-align: center;
  font-size: 1em;
  background-color: transparent;
  color: #CECECE;
}

.board {
  padding-bottom: 20px;
}

.board__row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.board__row:after {
  content: "";
  clear: both;
  display: block;
}

.instructions {
  font-size: 0.8em;
  margin-bottom: 10px;
}



